    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: var(--vscode-editor-background, #1e1e1e);
    color: var(--vscode-editor-foreground, #cccccc);
    font-family: var(--vscode-font-family, -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif);
    font-size: var(--vscode-font-size, 13px);
}

#viewer-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 50px;
    overflow: hidden;
}

#viewer-container canvas {
    display: block;
    width: 100%;
    height: 100%;
}

#toolbar {
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 4px;
    padding: 4px 8px;
    background-color: var(--vscode-editor-background, #252526);
    border: 1px solid var(--vscode-panel-border, #3c3c3c);
    border-radius: 4px;
    z-index: 100;
}

#toolbar button {
    padding: 4px 10px;
    background-color: var(--vscode-button-secondaryBackground, #3a3d41);
    color: var(--vscode-button-secondaryForeground, #cccccc);
    border: none;
    border-radius: 3px;
    cursor: pointer;
    font-size: 12px;
    min-width: 30px;
}

#toolbar button:hover {
    background-color: var(--vscode-button-secondaryHoverBackground, #45494e);
}

#toolbar button:active {
    background-color: var(--vscode-button-background, #0e639c);
}

#toolbar button.active {
    background-color: var(--vscode-button-background, #0e639c);
    color: var(--vscode-button-foreground, #ffffff);
}

#toolbar .separator {
    color: var(--vscode-panel-border, #3c3c3c);
    padding: 0 4px;
    align-self: center;
}

#status-bar {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    background-color: var(--vscode-statusBar-background, #007acc);
    color: var(--vscode-statusBar-foreground, #ffffff);
    font-size: 12px;
}

#status-text {
    flex: 1;
}

#coords {
    font-family: monospace;
    margin-left: 10px;
}

#loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    color: var(--vscode-editor-foreground, #cccccc);
    z-index: 200;
    background-color: rgba(30, 30, 30, 0.9);
    padding: 20px 40px;
    border-radius: 8px;
}

.spinner {
    width: 40px;
    height: 40px;
    border: 3px solid var(--vscode-panel-border, #3c3c3c);
    border-top-color: var(--vscode-button-background, #0e639c);
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Layer panel */
#layer-panel {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 220px;
    max-height: calc(100% - 100px);
    background-color: var(--vscode-editor-background, #252526);
    border: 1px solid var(--vscode-panel-border, #3c3c3c);
    border-radius: 4px;
    z-index: 100;
    display: none;
    flex-direction: column;
}

#layer-panel.visible {
    display: flex;
}

.layer-panel-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 10px;
    border-bottom: 1px solid var(--vscode-panel-border, #3c3c3c);
    background-color: var(--vscode-sideBarSectionHeader-background, #2d2d2d);
}

.layer-panel-header h4 {
    margin: 0;
    font-size: 12px;
    font-weight: 600;
}

.layer-panel-actions {
    display: flex;
    gap: 4px;
}

.layer-panel-actions button {
    padding: 2px 6px;
    font-size: 10px;
    background-color: var(--vscode-button-secondaryBackground, #3a3d41);
    color: var(--vscode-button-secondaryForeground, #cccccc);
    border: none;
    border-radius: 2px;
    cursor: pointer;
}

.layer-panel-actions button:hover {
    background-color: var(--vscode-button-secondaryHoverBackground, #45494e);
}

#layer-list {
    overflow-y: auto;
    padding: 4px 0;
    max-height: 400px;
}

.layer-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 4px 10px;
    font-size: 11px;
    cursor: pointer;
    transition: background-color 0.1s;
}

.layer-item:hover {
    background-color: var(--vscode-list-hoverBackground, #2a2d2e);
}

.layer-item input[type="checkbox"] {
    margin: 0;
    cursor: pointer;
}

.layer-color {
    width: 14px;
    height: 14px;
    border-radius: 2px;
    border: 1px solid var(--vscode-panel-border, #3c3c3c);
    flex-shrink: 0;
}

.layer-name {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.layer-count {
    color: var(--vscode-descriptionForeground, #888888);
    font-size: 10px;
}

/* Annotation mode indicator */
.annotation-mode {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--vscode-inputValidation-warningBackground, #352a05);
    border: 1px solid var(--vscode-inputValidation-warningBorder, #b89500);
    color: var(--vscode-inputValidation-warningForeground, #cca700);
    padding: 4px 12px;
    border-radius: 4px;
    font-size: 12px;
    z-index: 100;
    display: none;
}

.annotation-mode.visible {
    display: block;
}

/* Context menu */
.context-menu {
    position: absolute;
    background-color: var(--vscode-menu-background, #252526);
    border: 1px solid var(--vscode-menu-border, #454545);
    border-radius: 4px;
    padding: 4px 0;
    min-width: 150px;
    z-index: 300;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.context-menu-item {
    padding: 6px 20px;
    cursor: pointer;
    font-size: 12px;
}

.context-menu-item:hover {
    background-color: var(--vscode-menu-selectionBackground, #094771);
}

.context-menu-separator {
    height: 1px;
    background-color: var(--vscode-menu-separatorBackground, #454545);
    margin: 4px 0;
}
